$(function () {
  var bannerIcons = $(".banner__icon");
  var currentBannerIcon = 0;
  var intervalRef;
  var timeAutoPlay = 3000;
  var autoStart = true;

  function updateNav() {
    if (window.innerWidth < 991) {
      $('.navbar').addClass('bg-light');
      return;
    }

    checkNav();

    $(window).on('scroll', function (event) {
      checkNav();
      showFixBanner()
    });
  }

  function checkNav() {
    if (window.scrollY > 50) {
      $('.navbar').addClass('bg-light');
    } else {
      $('.navbar').removeClass('bg-light');
    }
  }

  function showFixBanner() {
    if (window.scrollY > $(".banner").height()) {
      $(".fix-banner").addClass("fix-banner--active");
    } else {
      $(".fix-banner").removeClass("fix-banner--active");
    }
  }


  /**
   * Change Slogans
   */
  function changeSlogans(number) {
    $(".banner__slogan").hide();
    $($(".banner__slogan")[number]).fadeIn();
  }

  /**
   * Update Autoplay
   * @param {Number} number 
   */
  function changeIconBanner(number) {
    var icon = document.querySelectorAll(".js-banner")[number];
    icon.setAttribute("style", "");
    icon.setAttribute("style", "animation: bounceIn .5s");
    icon.addEventListener("animationend", function (event) {
      event.target.setAttribute("style", "");
    })
  }



  /**
   * Auto play
   */
  function changeBanner() {
    console.log(currentBannerIcon)
    changeSlogans(currentBannerIcon);
    changeIconBanner(currentBannerIcon);
  }

  /**
   * Start auto paly
   */
  function startAutoPlay() {
    if (autoStart) {
      clearInterval(intervalRef);

      intervalRef = setInterval(function () {
        if (currentBannerIcon == bannerIcons.length - 1) {
          currentBannerIcon = 0;
        } else {
          currentBannerIcon++;
        }
        changeBanner();
      }, timeAutoPlay);
    }
  }

  /**
   * Stop Auto play
   */
  function stopAutoPlay() {
    clearInterval(intervalRef);
    startAutoPlay();
  }

  /**
   * onClick
   * @param {Node} event 
   */
  function onBannerIconClick(event) {
    var numberIcon = event.target.getAttribute("data-number");
    if (numberIcon != currentBannerIcon) {
      currentBannerIcon = numberIcon;
      changeBanner();
    } else {
      changeIconBanner(currentBannerIcon);
    }
    stopAutoPlay();
  }

  $(".banner__icons").on("click", ".js-banner", onBannerIconClick);

  $('.navbar-nav').on('click', ".nav-link", function (event) {
    let NAV_HEIGHT = $('.navbar').height > 50 ? $('.navbar ').height : 50;
    let position = $($(this).attr('href')).offset().top - NAV_HEIGHT;
    $('html, body').animate({
      scrollTop: position,
    }, 500);
    if ($('.navbar-collapse').hasClass('show')) {
      $('.js-menuToggle').click();
    }
    event.preventDefault();
  });

  $(".js-servicesFor").slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    fade: true,
  });

  $('.js-servicesNav').slick({
    slidesToShow: 4,
    slidesToScroll: 1,
    asNavFor: '.js-servicesFor',
    focusOnSelect: true,
  });

  $('.js-hospitalSlice').slick({
    slidesToShow: 4,
    focusOnSelect: true,
    prevArrow: `<button type="button" class="slick-prev slick-prev--big slick-prev--big slick-arrow">Previous</button>`,
    nextArrow: `<button type="button" class="slick-next slick-next--big slick-arrow">Next</button>`,
    responsive: [{
        breakpoint: 678,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  });


  /**
   * remove loading when page loaded
   */
  function loading() {
    $('.loading').fadeOut();
  }

  if (bannerIcons.length) startAutoPlay();

  loading();
  updateNav();
  new WOW().init({
    offset: 50
  });

});
